module.exports = "<router-outlet></router-outlet>\n\n<nav class=\"sidenav\" [clr-nav-level]=\"2\">\n  <section class=\"sidenav-content\">\n    <a class=\"nav-link\" routerLink=\"/news\" routerLinkActive=\"active\"\n       [routerLinkActiveOptions]=\"{exact:true}\">Current Release ({{current}})</a>\n\n    <section *ngFor=\"let minor of minors; let i = index\" class=\"nav-group collapsible\">\n      <input id=\"tab{{i}}\" type=\"checkbox\" checked>\n      <label for=\"tab{{i}}\">{{minor}} Changelogs</label>\n      <ul class=\"nav-list\">\n        <li *ngFor=\"let patch of patches[minor]\">\n          <a class=\"nav-link\" [routerLink]=\"patch\" routerLinkActive=\"active\">{{patch}}</a>\n        </li>\n      </ul>\n    </section>\n  </section>\n</nav>\n"

