module.exports = "<h2>Bug Fixes</h2>\n<ul class=\"list\">\n    <li bug-fix>Website is broken in Safari\n    </li>\n    <li bug-fix>Component pages don&#39;t work in IE10 and IE11\n    </li>\n</ul>"

