module.exports = "<h2>Highlights</h2>\n<ul class=\"list-unstyled whats-new-highlights\">\n    <li new-component>\n        <h6>Wizard Component</h6>\n        <p>\n            0.5.3 includes a new Angular component - Wizards. A wizard can be created using the following markup:\n        </p>\n        <pre>\n    <code class=\"language-html\">\n    &lt;clr-wizard #wizard [(clrWizardOpen)]=&quot;open&quot; [clrWizardSize]=&quot;'lg'&quot;&gt;\n        &lt;div class=&quot;wizard-title&quot;&gt;Wizard Title&lt;/div&gt;\n\n        &lt;clr-wizard-step&gt;Step 1&lt;/clr-wizard-step&gt;\n        &lt;clr-wizard-step&gt;Step 2&lt;/clr-wizard-step&gt;\n        &lt;clr-wizard-step&gt;Step 3&lt;/clr-wizard-step&gt;\n\n        &lt;clr-wizard-page&gt;Content for step 1&lt;/clr-wizard-page&gt;\n        &lt;clr-wizard-page&gt;Content for step 2&lt;/clr-wizard-page&gt;\n        &lt;clr-wizard-page&gt;Content for step 3&lt;/clr-wizard-page&gt;\n    &lt;/clr-wizard&gt;\n    </code>\n</pre>\n    </li>\n</ul>\n\n<h2>Bug Fixes</h2>\n<ul class=\"list\">\n    <li bug-fix>Inverse button: disabled inverse button is too dark</li>\n</ul>\n\n<h2>Other Changes</h2>\n<ul class=\"list\">\n    <li>Documentation for Wizard Component</li>\n    <li>Add spinner component size variations</li>\n    <li>Restructure the left side navigation on the components page of the website</li>\n    <li>Improvements to the login page component</li>\n</ul>"

