module.exports = "<h2>Highlights</h2>\n<ul class=\"list-unstyled whats-new-highlights\">\n  <li>\n    <h6>Move Clarity to the Metropolis font</h6>\n    <p>\n      To prepare for the future of Clarity's beta and beyond, Clarity has\n      adopted the new OFL-licensed typeface, Metropolis. <a\n      href=\"https://github.com/chrismsimpson/Metropolis\"\n      target=\"_blank\">Find out more information about Metropolis.</a>\n    </p>\n  </li>\n</ul>\n\n<h2>Breaking Changes</h2>\n<ul class=\"list-unstyled whats-new-highlights\">\n  <li breaking-change>\n    <h6>Upgrade BS4 to Alpha 5</h6>\n    <p>\n      Clarity has been upgraded to the latest alpha version of Bootstrap\n      4: Bootstrap 4, alpha 5. In its latest alpha release, the Bootstrap\n      team has changed a number of utility classes — like\n      <code class=\"clr-code\">float-*-left</code> instead of\n      <code class=\"clr-code\">pull-*-left</code>. In some cases, this may\n      constitute a breaking change for applications that were dependent\n      on the old utility classnames. <a\n      href=\"https://blog.getbootstrap.com/2016/10/19/bootstrap-4-alpha-5/\"\n      target=\"_blank\">Find out more information about Bootstrap Alpha 5's changes.</a>\n    </p>\n  </li>\n  <li breaking-change>\n    <h6>Move Clarity to the Metropolis font</h6>\n    <p>\n      As a precaution, make sure to check out your application after\n      upgrading to ClarityUI 0.6.0 to make sure the change to the\n      Metropolis font hasn’t had a negative impact on your application.\n      Metropolis is very close to the previous font that Clarity was\n      using but it is a different font. The Clarity team have not\n      encountered any negative side effects with the font change but want\n      to promote awareness around it.\n    </p>\n  </li>\n</ul>\n\n<h2>Bug Fixes</h2>\n<ul class=\"list\">\n  <li bug-fix>Fixed issue with Clarity Icon dependencies</li>\n  <li bug-fix>Content area on website has too much padding at top</li>\n</ul>\n\n<h2>Other Changes</h2>\n<ul class=\"list\">\n  <li>Implement font replacement</li>\n  <li>Font changes to website</li>\n  <li>Changes to Clarity font based on spec updates</li>\n  <li>Move Clarity-UI to a more scalable folder structure</li>\n  <li>Move code to github</li>\n  <li>Prepare to publish on NPM</li>\n  <li>Header &amp; Navigation specs - Update based on implementation &amp; add responsive specs</li>\n  <li>Move Text from Component Demo Files to .md Files</li>\n</ul>\n"

