module.exports = "<h2>Highlights</h2>\n<ul class=\"list-unstyled whats-new-highlights\">\n  <li>\n    <h6>New Plunkr Template for Clarity</h6>\n    <p>\n      With 0.7.3, Clarity has added a <a href=\"https://plnkr.co/edit/VPxyl7?p=preview\">Plunkr template</a>\n      to make it easier to test Clarity and submit bug reports.\n    </p>\n  </li>\n</ul>\n\n<h2>Bug Fixes</h2>\n<ul class=\"list\">\n  <li bug-fix>Replaced the SVG for the login background to use inline styles instead of the style tag to fix the IE 11 - Windows 10 issue with login backgrounds</li>\n\n  <li bug-fix>Fixed horizontal scroll issues when website is viewed on small devices</li>\n\n  <li bug-fix>Fixed a bug where the placeholder image and projected text were not displayed if the data was loading in the datagrid.</li>\n\n  <li bug-fix>Fixed the compilation error in the datagrid test file</li>\n\n  <li bug-fix>Fixed momentum scrolling issue when website was viewed on iPad</li>\n\n  <li bug-fix>Removed Dropdown Menu close transition to eliminate the delay when the dropdown menu was closed</li>\n</ul>\n\n<h2>Other Changes</h2>\n<ul>\n  <li>Added a dropdown with text toggle example in the header documentation</li>\n\n  <li>Updated the documentation to use the new icon markup for caret orientation</li>\n\n  <li>Reduced the distance of the dropdown caret from the text/image</li>\n</ul>\n"

