module.exports = "<h2>Highlights</h2>\n<ul class=\"list-unstyled whats-new-highlights\">\n  <li>\n    <h6>Added Angular CLI and Webpack 2 versions of Clarity Seed</h6>\n    <ul>\n      We now offer Clarity Seed project in three different build systems:\n      <li>1. Angular-CLI version (branch: master)</li>\n      <li>2. Webpack 2 version (branch: webpack)</li>\n      <li>3. SystemJS version (branch: systemjs)</li>\n    </ul>\n  </li>\n  <li>\n    <h6>Added new options to the Wizard component</h6>\n    <p>\n      Added the <code class=\"clr-code\">clrWizardClosable</code> input\n      and <code class=\"clr-code\">clrWizardOnCancel</code> event to the wizard component.\n    </p>\n  </li>\n  <li>\n    <h6>Upgraded ClarityNG to Angular 2.2.4</h6>\n    <p>Clarity has been updated to the latest version of Angular</p>\n  </li>\n  <li>\n    <h6>Added Clarity Icons to the Sketch Template</h6>\n    <p>\n      Clarity Icons have been added to the Sketch Template. You can download the\n      Sketch Template\n      <a sketchTemplateLink version=\"0.7.3\">here</a>\n    </p>\n  </li>\n</ul>\n\n<h2>Bug Fixes</h2>\n<ul class=\"list\">\n  <li bug-fix>Fixed Datagrid Demo on IE10</li>\n</ul>\n\n<h2>Other Changes</h2>\n<ul>\n  <li>\n    Added the <code>.icon-color-error</code> class and unified the color of\n    <code>.icon-color-danger</code> and <code>.icon-color-warning</code> classes\n  </li>\n  <li>\n    Added a demo for <code>.modal-xl</code> in the Modal documentation.\n  </li>\n  <li>\n    Updated the Typography documentation to add Card Header/Title to the H4 styles\n  </li>\n</ul>\n"

